<template>
  <div class="highquality">
    <div class="song-list">
      <div class="song-title">精品歌单</div>
      <div class="details" v-if="highqualityList != null">
        <div
          class="remd_li"
          v-for="(value, index) in highqualityList.slice(0, 10)"
          :key="index"
          @click="remddetail(value.name, value.id)"
        >
          <div class="remd_img">
            <img :src="value.coverImgUrl" alt="" />
          </div>
          <div class="remd_text">{{ value.name }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  data () {
    return {
      highqualityList: []
    }
  },
  mounted () {
    this.highquality()
  },
  methods: {
    highquality () {
      this.api.highquality().then((res) => {
        if (res.data.code == 200) {
          this.highqualityList = res.data.playlists
          console.log(res)
        } else {
          Toast(res.data.msg)
        }
      })
        .catch((err) => {
          Toast(err)
        })
    },
  }
}
</script>

<style lang="less" scoped>
.highquality {
  padding: 0 20px;
  margin-top: 30px;

  .song-list {
    width: 100%;
    margin-top: 32px;

    .song-title {
      font-size: 32px;
      text-align: left;
    }
    .details {
      width: 100%;
      padding: 20px 0;
      display: flex;
      overflow-x: auto;

      .remd_li {
        height: 270px;

        .remd_img {
          width: 220px;
          height: 220px;
          border-radius: 16px;
          overflow: hidden;
          margin-right: 20px;

          img {
            width: 100%;
            height: 100%;
          }
        }
        .remd_text {
          width: 220px;
          font-size: 24px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin-top: 10px;
        }
      }
    }
  }
}
</style>