<template>
  <div class="banner">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item, index) in bannerlist" :key="index">
        <img :src="item.pic" alt />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  data() {
    return {
      bannerlist: [],
    }
  },
  mounted() {
    this.banner()
  },
  methods: {
    banner() {
      Toast.loading({
        message: '加载中...',
        forbidClick: true,
        overlay: true,
      })
      let data = {
        type: 2,
      }
      this.api
        .banner(data)
        .then((res) => {
          if (res.data.code == 200) {
            this.bannerlist = res.data.banners
          } else {
            Toast(res.data.msg)
          }
        })
        .catch((err) => {
          Toast(err)
        })
    },
  },
}
</script>

<style lang="less" scoped="scoped">
.banner {
  height: 350px;

  .van-swipe {
    width: 100%;
    height: 100%;

    .van-swipe__track {
      width: 100%;
      height: 100%;

      .van-swipe-item {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          width: 95%;
          height: 95%;
          border-radius: 20px;
        }
      }
    }
    ::v-deep.van-swipe__indicator--active {
      background-color: crimson !important;
    }
  }
}
</style>