<template>
  <div class="newsong">
    <div class="song-title">推荐新音乐</div>
    <van-cell is-link v-for="(value, index) in newsongList" :key="index">
      <template #title>
        <span class="custom-title">{{ value.name }}</span>
        <div class="Introduction">
          <span v-for="(val, index) in value.song.artists" :key="index">{{
            val.name + '-'
          }}</span>
          <span>{{ value.song.album.name }}</span>
        </div>
      </template>
      <template #right-icon>
        <van-icon name="play-circle-o" class="play-circle-o" size="20" />
      </template>
    </van-cell>
  </div>
</template>

<script>
export default {
  data () {
    return {
      newsongList: []
    }
  },
  mounted () {
    this.newsong()
  },
  methods: {
    newsong () {
      this.api.newsong().then(res => {
        this.newsongList = res.data.result
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

